<template>
  <!--START ABOUT-US-->
  <section class="section" id="achievement_paper">
    <div>
      <div style="width: 100%;" id="paper">
        <el-form ref="form" :model="sizeForm" label-width="80px" size="mini">
          <el-form-item label="" style="margin-bottom: 5px;">
            <el-checkbox-group v-model="sizeForm.type" @change="findByConditions">
              <el-checkbox-button label="Journal" name="type"></el-checkbox-button>
              <el-checkbox-button label="Conference" name="type"></el-checkbox-button>
            </el-checkbox-group>
          </el-form-item>
          <el-form-item label="" style="margin-bottom: 5px;">
            <el-checkbox-group v-model="sizeForm.published" @change="findByConditions">
              <el-checkbox-button label="1" name="published">Published</el-checkbox-button>
              <el-checkbox-button label="0" name="published">Accepted</el-checkbox-button>
            </el-checkbox-group>
          </el-form-item>
          <el-form-item label="CCF" style="margin-bottom: 5px;">
            <el-checkbox-group v-model="sizeForm.CCF" @change="findByConditions">
              <el-checkbox-button label="1" name="CCF">T1A</el-checkbox-button>
              <el-checkbox-button label="2" name="CCF">A</el-checkbox-button>
              <el-checkbox-button label="3" name="CCF">B</el-checkbox-button>
              <el-checkbox-button label="4" name="CCF">C</el-checkbox-button>
              <el-checkbox-button label="5" name="CCF">A</el-checkbox-button>
              <el-checkbox-button label="6" name="CCF">B</el-checkbox-button>
              <el-checkbox-button label="7" name="CCF">C</el-checkbox-button>
              <el-checkbox-button label="0" name="CCF"></el-checkbox-button>
            </el-checkbox-group>
          </el-form-item>
          <el-form-item label="" style="margin-bottom: 5px;">
            <el-checkbox-group v-model="sizeForm.SCI" @change="findByConditions">
              <el-checkbox-button label="1" name="SCI">SCI </el-checkbox-button>
              <el-checkbox-button label="2" name="SCI">SCI </el-checkbox-button>
              <el-checkbox-button label="3" name="SCI">SCI </el-checkbox-button>
              <el-checkbox-button label="4" name="SCI">SCI </el-checkbox-button>
              <el-checkbox-button label="EI" name="SCI">EI</el-checkbox-button>
              <el-checkbox-button label="0" name="SCI"></el-checkbox-button>
            </el-checkbox-group>
          </el-form-item>
        </el-form>
        <el-divider content-position="center"> {{ tableData.length }} </el-divider>
        <el-table :data="tableData">
          <el-table-column type="index" min-width="50">
          </el-table-column>
          <el-table-column prop="introduction" label="" min-width="600" header-align="center">
          </el-table-column>
          <el-table-column prop="type" label="" min-width="110" align="center">
            <template slot-scope="scope">
              <el-tag :type="scope.row.type != 'Conference' ? 'success' : 'info'" disable-transitions>{{ scope.row.type
                }}</el-tag>
            </template>
          </el-table-column>
          <el-table-column prop="year" label="" min-width="100" align="center">
          </el-table-column>
          <el-table-column prop="CCF" label="CCF" min-width="150" align="center">
            <template slot-scope="scope">
              <el-tag :type="scope.row.CCF != 0 ? 'primary' : 'danger'" disable-transitions>{{ CCF_info[scope.row.CCF]
                }}</el-tag>
            </template>
          </el-table-column>
          <el-table-column prop="SCI" label="" min-width="150" align="center">
            <template slot-scope="scope">
              <el-tag v-if="scope.row.SCI != 0" :type="'primary'" disable-transitions>{{ SCI_info[scope.row.SCI]
                }}</el-tag>
              &nbsp;&nbsp;&nbsp;&nbsp;
              <el-tag v-if="scope.row.EI != 0" :type="'warning'" disable-transitions>EI</el-tag>
              <el-tag v-if="scope.row.EI == 0 && scope.row.SCI == 0" :type="'danger'" disable-transitions></el-tag>
            </template>
          </el-table-column>
          <el-table-column prop="published" label="" min-width="100" align="center">
            <template slot-scope="scope">
              <el-tag v-if="scope.row.published == 1" type="success" effect="plain">
                Published
              </el-tag>
              <el-tag v-else type="info" effect="plain">
                Accepted
              </el-tag>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
  </section>
  <!--END ABOUT-US-->
</template>

<script>
/**
 * About component
 */
import axios from "axios";
export default {
  name: 'AchievementPaper',
  data() {
    return {
      sizeForm: {
        SCI: [],
        type: [],
        CCF: [],
        published: []
      },
      CCF_info: [
        "",
        "T1A",
        "A",
        "B",
        "C",
        "A",
        "B",
        "C"
      ],
      SCI_info: [
        "SCI",
        "SCI ",
        "SCI ",
        "SCI ",
        "SCI ",
      ],
      tableData: [],
    }
  },
  created() {
    this.getData()
  },
  methods: {
    getData() {
      axios.get(`${process.env.VUE_APP_API_ENDPOINT}/paper/findAll`).then((res) => {
        console.log(res);
        this.tableData = res.data
        console.log('000');
      }).catch((err) => {
        console.log(err);
      })
    },
    findByConditions() {
      console.log(this.sizeForm);
      axios.post(`${process.env.VUE_APP_API_ENDPOINT}/paper/findByConditions`, this.sizeForm).then((res) => {
        console.log(res);
        this.tableData = res.data
      }).catch((err) => {
        console.log(err);
      })
    }
  }
}
</script>

<style>
.section {
  margin-top: -60px;
}
</style>
