<template>
  <!--START ABOUT-US-->
  <section class="section" id="achievement_patent">
    <div class="container">
      <div style="width: 100%;" id="paper">
        <el-form ref="form" :model="sizeForm" label-width="120px">
          <el-form-item label="">
            <el-radio-group v-model="sizeForm.authorized" size="small" @change="updateData">
              <el-radio border label=""></el-radio>
              <el-radio border label=""></el-radio>
              <el-radio border label=""></el-radio>
            </el-radio-group>
          </el-form-item>
        </el-form>
        <el-divider content-position="center"> {{ tableData.length }} </el-divider>
        <el-table :data="tableData">
          <el-table-column type="index" min-width="80" align="center">
          </el-table-column>
          <el-table-column prop="author" label="" min-width="350" align="center">
          </el-table-column>
          <el-table-column prop="title" label="" min-width="400" align="center">
          </el-table-column>
          <el-table-column prop="patentId" label="" min-width="150" align="center">
          </el-table-column>
          <el-table-column prop="authorized" label="" min-width="100" align="center">
            <template slot-scope="scope">
              <el-tag v-if="scope.row.authorized != 0" type="primary" disable-transitions></el-tag>
              <el-tag v-if="scope.row.authorized == 0" type="warning" disable-transitions></el-tag>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
  </section>
  <!--END ABOUT-US-->
</template>

<script>
/**
 * About component
 */
import axios from "axios";
export default {
  name: 'AchievementPatent',
  data() {
    return {
      tableData: [
        // {
        //   author: ', , , ',
        //   title: '',
        //   patentId: '2024100997165',
        //   authorized: 0,
        // },
        // {
        //   author: ',, , , , ',
        //   title: 'Solidity',
        //   patentId: '2020115620731',
        //   authorized: 1,
        // },
      ],
      sizeForm: {
        authorized: '',
      }
    }
  },
  created() {
    this.getData()
  },
  methods: {
    getData() {
      axios.get(`${process.env.VUE_APP_API_ENDPOINT}/patent/findAll`).then((res) => {
        console.log(res);
        this.tableData = res.data
        console.log('000');
      }).catch((err) => {
        console.log(err);
      })
    },
    getDataByAuthorized(authorized){
      axios.get(`${process.env.VUE_APP_API_ENDPOINT}/patent/findByAuthorized/${authorized}`).then((res) => {
            this.tableData = res.data
          }).catch((err) => {
            console.log(err);
          })
    },
    updateData() {
      if (this.sizeForm.authorized == '') {
        this.getData();
      } else {
        if (this.sizeForm.authorized == '') {
          this.getDataByAuthorized(1)
        }else{
          this.getDataByAuthorized(0)
        }
      }
    }
  }
}
</script>

<style>
.section {
  margin-top: -60px;
}
</style>
