<template>
    <!--START ABOUT-US-->
    <section class="section" id="achievement_copyright">
        <div class="container">
            <el-divider content-position="center"> {{ tableData.length }} </el-divider>
            <div style="width: 70%;margin-left: 15%;" id="paper">
                <el-table :data="tableData">
                    <el-table-column type="index" min-width="80" align="center">
                    </el-table-column>
                    <el-table-column prop="title" label="" min-width="400" align="center">
                    </el-table-column>
                    <el-table-column prop="registrationNum" label="" min-width="150" align="center">
                    </el-table-column>
                </el-table>
            </div>
        </div>
    </section>
    <!--END ABOUT-US-->
</template>

<script>
/**
 * About component
 */
import axios from "axios";
export default {
    name: 'AchievementCopyright',
    data() {
        return {
            tableData: [],
        }
    },
    created() {
        this.getData()
    },
    methods: {
        getData() {
            axios.get(`${process.env.VUE_APP_API_ENDPOINT}/copyright/findAll`).then((res) => {
                console.log(res);
                this.tableData = res.data
                console.log('000');
            }).catch((err) => {
                console.log(err);
            })
        },
        updateData() {
            if (this.sizeForm.authorized == '') {
                this.getData();
            } else {
                if (this.sizeForm.authorized == '') {
                    this.getDataByAuthorized(1)
                } else {
                    this.getDataByAuthorized(0)
                }
            }
        }
    }
}
</script>

