<template>
    <div>
        <el-header style="text-align: center; font-size: 12px">
            <el-dropdown>
                <i class="el-icon-setting" style="margin-right: 15px"></i>
            </el-dropdown>
            <span>-</span>
        </el-header>
        <el-container style="border: 1px solid #eee;height: 100%;">
            <el-aside width="200px" style="background-color: rgb(238, 241, 246);">
                <el-menu :default-openeds="['3']" default-active="3-1">
                    <el-menu-item class="active" index="1" onclick="location.href='/admin/manageStudents'"><i
                            class="el-icon-message"></i></el-menu-item>
                    <el-menu-item index="2" onclick="location.href='/'"><i
                            class="el-icon-message"></i></el-menu-item>
                    <el-submenu index="3">
                        <template slot="title"><i class="el-icon-setting"></i></template>
                        <el-menu-item-group>
                            <el-menu-item index="3-1" onclick="location.href='/admin/managePapers'"></el-menu-item>
                            <el-menu-item index="3-2" onclick="location.href='/admin/managePatents'"></el-menu-item>
                            <el-menu-item index="3-3" onclick="location.href='/admin/manageCopyrights'"></el-menu-item>
                        </el-menu-item-group>
                    </el-submenu>
                </el-menu>
            </el-aside>
            <el-container>
                <el-main>
                    <el-button type="primary" @click="addPaper"></el-button>
                    <el-table :data="tableData" height="760">
                        <el-table-column type="index" min-width="50">
                        </el-table-column>
                        <el-table-column prop="introduction" label="" min-width="450" header-align="center">
                        </el-table-column>
                        <el-table-column prop="type" label="" min-width="110" align="center">
                            <template slot-scope="scope">
                                <el-tag :type="scope.row.type != 'Conference' ? 'success' : 'info'"
                                    disable-transitions>{{ scope.row.type }}</el-tag>
                            </template>
                        </el-table-column>
                        <el-table-column prop="year" label="" min-width="100" align="center">
                        </el-table-column>
                        <el-table-column prop="CCF" label="CCF" min-width="150" align="center">
                            <template slot-scope="scope">
                                <el-tag :type="scope.row.CCF != 0 ? 'primary' : 'danger'" disable-transitions>{{
                                    CCF_info[scope.row.CCF]
                                    }}</el-tag>
                            </template>
                        </el-table-column>
                        <el-table-column prop="SCI" label="" min-width="150" align="center">
                            <template slot-scope="scope">
                                <el-tag v-if="scope.row.SCI != 0" :type="'primary'" disable-transitions>{{
                                    SCI_info[scope.row.SCI]
                                    }}</el-tag>
                                &nbsp;&nbsp;&nbsp;&nbsp;
                                <el-tag v-if="scope.row.EI != 0" :type="'warning'" disable-transitions>EI</el-tag>
                                <el-tag v-if="scope.row.EI == 0 && scope.row.SCI == 0" :type="'danger'"
                                    disable-transitions></el-tag>
                            </template>
                        </el-table-column>
                        <el-table-column prop="published" label="" min-width="100" align="center">
                            <template slot-scope="scope">
                                <el-tag v-if="scope.row.published == 1" type="success" effect="plain">
                                    Published
                                </el-tag>
                                <el-tag v-else type="info" effect="plain">
                                    Accepted
                                </el-tag>
                            </template>
                        </el-table-column>
                        <el-table-column prop="published" label="" min-width="110" align="center">
                            <template slot-scope="scope">
                                <el-button @click="editPaper(scope.row)" type="text" size="mini"></el-button>
                                <el-button type="danger" size="mini" @click="deletePaper(scope.row)"></el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-main>
            </el-container>
        </el-container>
        <el-dialog style="margin-top: -5%;" title="" :visible.sync="dialogVisible_add" width="50%"
            :before-close="handleClose_add">
            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                <el-form-item label="" prop="type">
                    <el-radio-group v-model="ruleForm.type" size="medium" @change="changeCCF">
                        <el-radio border label="Journal"></el-radio>
                        <el-radio border label="Conference"></el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item label="" prop="author">
                    <el-input v-model="ruleForm.author" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" prop="year">
                    <el-input v-model="ruleForm.year"></el-input>
                </el-form-item>
                <el-form-item label="" prop="introduction">
                    <el-input type="textarea" v-model="ruleForm.introduction" placeholder=". . "
                        :rows="4"></el-input>
                </el-form-item>
                <el-form-item label="DOI" prop="href">
                    <el-input v-model="ruleForm.href"></el-input>
                </el-form-item>
                <el-form-item label="CCF" prop="CCF">
                    <el-select v-model="ruleForm.CCF" placeholder="CCF" @change="changeType">
                        <el-option v-if="ruleForm.type == 'Journal'" label="T1A" value="1"></el-option>
                        <el-option v-if="ruleForm.type == 'Journal'" label="A" value="2"></el-option>
                        <el-option v-if="ruleForm.type == 'Journal'" label="B" value="3"></el-option>
                        <el-option v-if="ruleForm.type == 'Journal'" label="C" value="4"></el-option>
                        <el-option v-if="ruleForm.type == 'Conference'" label="A" value="5"></el-option>
                        <el-option v-if="ruleForm.type == 'Conference'" label="B" value="6"></el-option>
                        <el-option v-if="ruleForm.type == 'Conference'" label="C" value="7"></el-option>
                        <el-option label="" value="0"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="EI" prop="EI">
                    <el-radio-group v-model="ruleForm.EI">
                        <el-radio label="1"></el-radio>
                        <el-radio label="2"></el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item label="SCI" prop="SCI">
                    <el-select v-model="ruleForm.SCI" placeholder="SCI">
                        <el-option label="SCI 1" value="1"></el-option>
                        <el-option label="SCI 2" value="2"></el-option>
                        <el-option label="SCI 3" value="3"></el-option>
                        <el-option label="SCI 4" value="4"></el-option>
                        <el-option label="" value="0"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" prop="published">
                    <el-radio-group v-model="ruleForm.published" size="medium">
                        <el-radio border label="1">Published</el-radio>
                        <el-radio border label="0">Accepted</el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item>
                    <el-button @click="dialogVisible_add = false"> </el-button>
                    <el-button type="primary" @click="submitAdd('ruleForm')"></el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
        <el-dialog style="margin-top: -5%;" title="" :visible.sync="dialogVisible_edit" width="50%"
            :before-close="handleClose">
            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                <el-form-item label="" prop="type">
                    <el-radio-group v-model="ruleForm.type" size="medium" @change="changeCCF">
                        <el-radio border label="Journal"></el-radio>
                        <el-radio border label="Conference"></el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item label="" prop="author">
                    <el-input v-model="ruleForm.author" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" prop="year">
                    <el-input v-model="ruleForm.year"></el-input>
                </el-form-item>
                <el-form-item label="" prop="introduction">
                    <el-input type="textarea" v-model="ruleForm.introduction" placeholder=". . "
                        :rows="4"></el-input>
                </el-form-item>
                <el-form-item label="DOI" prop="href">
                    <el-input v-model="ruleForm.href"></el-input>
                </el-form-item>
                <el-form-item label="CCF" prop="CCF">
                    <el-select v-model="ruleForm.CCF" placeholder="CCF" @change="changeType">
                        <el-option v-if="ruleForm.type == 'Journal'" label="T1A" value="1"></el-option>
                        <el-option v-if="ruleForm.type == 'Journal'" label="A" value="2"></el-option>
                        <el-option v-if="ruleForm.type == 'Journal'" label="B" value="3"></el-option>
                        <el-option v-if="ruleForm.type == 'Journal'" label="C" value="4"></el-option>
                        <el-option v-if="ruleForm.type == 'Conference'" label="A" value="5"></el-option>
                        <el-option v-if="ruleForm.type == 'Conference'" label="B" value="6"></el-option>
                        <el-option v-if="ruleForm.type == 'Conference'" label="C" value="7"></el-option>
                        <el-option label="" value="0"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="EI" prop="EI">
                    <el-radio-group v-model="ruleForm.EI">
                        <el-radio label="1"></el-radio>
                        <el-radio label="0"></el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item label="SCI" prop="SCI">
                    <el-select v-model="ruleForm.SCI" placeholder="SCI">
                        <el-option label="SCI 1" value="1"></el-option>
                        <el-option label="SCI 2" value="2"></el-option>
                        <el-option label="SCI 3" value="3"></el-option>
                        <el-option label="SCI 4" value="4"></el-option>
                        <el-option label="" value="0"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" prop="published">
                    <el-radio-group v-model="ruleForm.published" size="medium">
                        <el-radio border label="1">Published</el-radio>
                        <el-radio border label="0">Accepted</el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item>
                    <el-button @click="dialogVisible_edit = false"> </el-button>
                    <el-button type="primary" @click="submitEdit('ruleForm')"></el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
    </div>
</template>


<script>
import axios from 'axios';

export default {
    name: "managePapers",
    data() {
        return {
            tableData: [],
            dialogVisible_edit: false,
            dialogVisible_add: false,
            ruleForm: {
                id: 0,
                type: '',
                author: '',
                introduction: '',
                href: '',
                CCF: '',
                SCI: '',
                EI: '',
                year: '',
                published: '',
                isDel: 0
            },
            rules: {
                type: [
                    { required: true, message: '', trigger: 'change' }
                ],
                author: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                introduction: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                year: [
                    { required: true, message: ' / ', trigger: 'blur' },
                ],
                CCF: [
                    { required: true, message: 'CCF', trigger: 'change' }
                ],
                SCI: [
                    { required: true, message: 'SCI', trigger: 'change' }
                ],
                EI: [
                    { required: true, message: 'EI', trigger: 'change' }
                ],
                published: [
                    { required: true, message: '', trigger: 'change' }
                ],

            },
            CCF_info: [
                "",
                "T1A",
                "A",
                "B",
                "C",
                "A",
                "B",
                "C"
            ],
            SCI_info: [
                "SCI",
                "SCI ",
                "SCI ",
                "SCI ",
                "SCI ",
            ],
        }
    },
    created() {
        this.getData();
    },
    methods: {
        getData() {
            axios.get("http://127.0.0.1:5000/paper/findAll").then((res) => {
                console.log(res);
                this.tableData = res.data
            }).catch((err) => {
                console.log(err);
            })
        },
        addPaper() {
            this.ruleForm = {
                id: 0,
                type: '',
                author: '',
                introduction: '',
                href: '',
                CCF: '0',
                SCI: '',
                EI: '',
                year: '',
                published: '',
                isDel: 0
            }
            this.dialogVisible_add = true
        },
        editPaper(paper) {
            this.ruleForm = paper
            this.dialogVisible_edit = true
            // console.log(this.ruleForm);
        },
        deletePaper(paper) {
            this.$confirm(`${paper.introduction}`, '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                axios.delete(`${process.env.VUE_APP_API_ENDPOINT}/paper/delete/${paper.id}`).then((res) => {
                    if (res.data == "success") {
                        this.$message({
                            message: '',
                            type: 'success'
                        });
                        this.getData()
                    } else {
                        this.$message.error('1');
                    }
                }).catch((err) => {
                    console.log("err:", err);
                    
                    this.$message.error('2');
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });
            });

        },
        handleClose(done) {
            done()
        },
        handleClose_add(done) {
            done()
        },
        submitAdd(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.$axios
                        .post(`${process.env.VUE_APP_API_ENDPOINT}/paper/add`, this.ruleForm)
                        .then((res) => {
                            this.$message({
                                message: '',
                                type: 'success'
                            });
                            console.log(res);
                            this.getData()
                            this.dialogVisible_add = false
                        });
                } else {
                    this.$message.error('');
                    return false;
                }
            });
        },
        submitEdit(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.$axios
                        .put(`${process.env.VUE_APP_API_ENDPOINT}/paper/update`, this.ruleForm)
                        .then((res) => {
                            this.$message({
                                message: '',
                                type: 'success'
                            });
                        });
                    this.dialogVisible_edit = false
                } else {
                    console.log('');
                    return false;
                }
            });
        },
        changeCCF() {
            if (this.ruleForm.type == "Journal") {
                if (parseInt(this.ruleForm.CCF) > 4) {
                    this.ruleForm.CCF = "0"
                }
            } else {
                if (parseInt(this.ruleForm.CCF) < 5) {
                    this.ruleForm.CCF = "0"
                }
            }
        },
        changeType() {
            console.log("changeType");
        }
    }
}
</script>

<style>
.el-header {
    background-color: #B3C0D1;
    color: #333;
    line-height: 60px;
}

.el-aside {
    color: #333;
}

.el-tooltip__popper {
    font-size: 14px;
    max-width: 30%
}
</style>